.context{
  display: flex;
  flex-direction: column;
}

.title{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 100%;
  text-align: center;
  color: #FFFFFF;
  margin-top: 20px;
}
.subTitle{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  text-align: center;
  color: #FFFFFF;
  margin: 8px 0 40px 0;
}
.tag{
  width: 100%;
  position: absolute;
  bottom: 0px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 8px;
}

.mainTex,.srcmainTex{
  width: 100%;
  bottom: 0px;
  font-style: normal;
  line-height: 1.5;
  text-align: left;
  font-weight: 400;
  font-size: 14px;
  color: #FFFFFF;
  box-sizing: border-box;
  padding: 0 30px;
}

.mainTex > b,.srcmainTex > b{
  font-size: 16px;
  font-weight: 700;
  line-height: 2;
}
.srcmainTex > b{
  color: #C3DDFA;
}

.link{
  color: #A5CCF7;
  text-decoration: underline;
}

.box{
  display: flex;
  justify-content: center;
  border: 1px solid;
  margin: 10px 20px 0 20px;
  padding-bottom: 20px;
}
.agreementBtn{
  height: 30px;
  width: 80px;
  line-height: 1.5;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  border: 1px solid;
  background-color: black;
  color: #C9C4FF;
  margin: 10px;
  border-radius: 20px;
}

.warningTex{
  line-height: 1.5;
  font-weight: 700;
  font-size: 24px;
  color: red;
}

.sendr_num, .my_num{
  margin-left: 10px;
  width: 200px;
}

.btnBox{
  display: flex;
  margin-left: 10px;
  gap: 10px;
}

.btnBox > div{
  height: 30px;
  width: 90px;
  line-height: 1.5;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  border: 1px solid;
  background-color: black;
  color: #87BBF5;
  margin: 10px;
  border-radius: 20px;
}

.btn{
  color: #00FFDC;
}

.boxContext{
  margin: 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.3);
}

.container {
  position: absolute;
  height: 200px;
  bottom: 50%;
  color:black;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  flex-direction: column;
  width: calc(100% - 48px);
  max-width: 500px;
  padding: 20px 16px;
  z-index: 11;
  border-radius: 15px;
  background-color: #fff;
  pointer-events: auto;
}
.PopupText{
  line-height: 1.5;
  font-weight: 700;
  font-size: 24px;
}
.PopupButton{
  height: 60px;
  width: 150px;
  line-height: 2;
  font-weight: 700;
  font-size: 28px;
  text-align: center;
  border: 1px solid;
  background-color: black;
  color: palevioletred;
  margin-top: 50px;
  border-radius: 30px;
}

.PopupButtonBox{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px
}